
import Box from 'vux/src/components/box/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import md5 from 'vux/src/tools/md5/index.js'

import verificationCode from '@/components/features/verificationCode'
import {checkMobile, checkPassword} from "../../utils/assist/regExp";

export default {
name: "registered",
components: {
Box,
Group,
XInput,
XButton,
verificationCode
},
data() {
return {

isActive: true,

verificationCode: '',
userPhone: '',
password: '',
phoneNum: ''
}
},
computed: {
registForm() {
return {
mobileCall: this.phoneNum,

chnlcd: '',
password: md5(this.password + 'TuD00Iqz4ge7gzIe2rmjSAFFKtaIBmnr8S'),

yqcode: '',

scene: 'regist',

smsCode: this.verificationCode
}
}
},
methods: {

getcode(phone) {

this.$http.post('/send/smsCodeNew', {scene: 'regist', sendSmsPhone: phone}).then(res => {
console.log(res)
})
},

getphone(phone) {
this.phoneNum = phone
},

regist() {
if (!this.isActive || !checkMobile(this.phoneNum || !checkPassword(this.password)) || this.smsCode) {
return
}
this.$http.post('/user/doRegister', this.registForm).then(res => {
console.log(res)
})
}
}
}



